@import './../../styles/color';

.finance-grafics{
    background-color: $box-background;
    width: 155px;
    height: 150px;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;

}

.finance-grafic-bar{
    margin-top: auto;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.finance-grafic-text{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 100px;
    margin-left: 10px;
}
.finance-category-cicle{
        width: 8px!important;
        height: 8px;
        border-radius: 8px;
        margin-right: 6px;
        padding: 0!important;
    
}
.finance-category{
    display: flex!important;
    p{
        color: $paragraph-color!important;
        min-width: 130px!important;

    }
}


.finance-grafic-text h5{
    margin: 5px 0px;
    font-size: 18px;
}

.finance-value{
    width: 10px;
  
  
  }
.finance-grafics-box{
    display: flex;
    gap: 10px;
    justify-content: space-between;
}

.finance-grafic-total{
    width: 200px;
    height: 150px;
    background-color: $box-background;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 4px;
}
.finance-grafic-total-grafic{
    width: 100px;
    margin-right: 10px;
}



.finance-table-box{
    width: 100%;
    div{
        padding: 4px 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 6px;
    }
}



.finance-table-title{
    font-weight: 900;
}


.finance-table-item{
    background-color: $box-background;
    p{
        min-width: 310px;
        color: $title-color;
    }
    &:hover{
        background-color: $todolist-background;
    }
}





.finance-table-btn button{
    background: none;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.3;
    transition: 0.4s;
    font-size: 12px;
    font-weight: 500;
    margin-right: 8px;
    max-width: 130px!important;


}

.finance-table-btn button:hover{
    opacity: 1;
}

.finance-table-btn img{
    width: 16px;
    margin-right: 3px;

}
.finance-table-btn {
    flex-direction: row;
    display: flex;
    height: 50px;
}


@media only screen and (max-width: 700px) {

.finance-grafics-box{
    flex-wrap: wrap;
    margin-top: 48px!Important
}

.finance-table-item{
    flex-wrap: wrap;
    padding-top: 16px!important;
    max-width: 98%!important;

}

.finance-table-box{
    flex-wrap: wrap;
    margin-top: 32px!important;
    margin-bottom: 128px!important;

}

.finance-value{
    width: 100px;
}

.finance-category{
    justify-content: flex-start!important;
    margin-left: -18px;
}

.finance-table-btn{
    height: 40px;
    padding: 0!Important;
}
    
}
