.toDoList-page-tables {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  flex-wrap: wrap;
  width: 100%;
}

.toDoList-page-simple-table {
  background-color: #fff;
  min-height: 120px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: 330px;
}

.toDoList-page-table-title {
  display: inline-block;
  border-radius: 4px;
  margin: 0px 0px 12px 8px;
  padding: 4px 6px;
  color: var(--6);
}

.toDoList-page-table-title {
  font-weight: 800;
  font-size: 15px;
  margin-top: 10px;
}

.toDoList-page-counter {
  color: var(--7);
}

.toDoList-page-simple-table div div div div .todolist-status {
  display: none;
}

.toDoList-page-simple-table div div div div.todolist-container {
  margin-top: 10px;
}/*# sourceMappingURL=ToDoListTrello.css.map */